/*card*/
.card {

    display: block;
    width: 250px;
    min-height: 280px;
    border: 1px solid #adb5bd;
    padding: 10px;
    color: #000000;
    text-decoration: none;
    margin-right: 10px;
    margin-bottom: 10px;

    border-radius: 0.5rem;
    transition: 0.3s;
}
.card:hover{
    box-shadow: 4px 10px 39px -14px rgba(0, 0, 0, 0.45);}

.card:hover .img-layout {
    opacity: 1;
}
.card:hover .link {
    transform: translateX(-6.3%);
    /*transform: scale(1.02);*/
}
.card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 0.5rem;
}
.card .img-wrapper {
    position: relative;
    overflow: hidden;
    color: #ffffff;

}
.card .img-layout {
    position: absolute;
    top: 0;
    left: 0;
    height: 98%;
    padding: 10px;
    background-color: #3a3a3aa8;
    opacity: 0;
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 0.5rem;
}
.card .img-layout p {
    margin: 0;
}
.card .link {
    display: flex;
    align-items: center;
    transform: translateX(-30%);
    transition: 0.3s;
    margin-bottom: 10px;
}
.card .link .arrow-prev {
    opacity: 0;
    transition: 0.3s;
    width: 15px;
    height: 15px;
}
.card .link .arrow-next {
    opacity: 1;
    transition: 0.3s;
    width: 15px;
    height: 15px;
    margin-left: 10px;
}
.card .link:hover {
    transform: translateX(0);
}
.card .link:hover .arrow-prev {
    opacity: 1;
    margin-right: 10px;
}
.card .link:hover .arrow-next {
    opacity: 0;
}