.custom-checkbox {
    position: relative;
    width: 150px;
    line-height: 1.5rem;
    margin-top: 0;
    margin-bottom: 0;

}
.custom-checkbox input {
    position: absolute;
    z-index: -1;
    opacity: 0;

}
.custom-checkbox input + label {
    display: inline-flex;
    align-items: center;
    user-select: none;
    transition: 0.3s;
    cursor: pointer;
}
.custom-checkbox input + label::before {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    flex-shrink: 0;
    flex-grow: 0;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%;
    background-image: url('/public/images/kolos.png');
    border: 2px solid transparent;
    object-fit: cover;
    transition: border 0.3s;
    margin-right: 5px;
}
.custom-checkbox:hover input + label {
    color: #6b7076;
}
.custom-checkbox:hover input + label::before {
    border: 2px solid #6b7076;
    background: transparent;
}
.custom-checkbox:hover .prompt {
    opacity: 1;
}
.custom-checkbox input:checked + label {
    color: #000000;
}
.custom-checkbox input:checked + label::before {
    border-color: #0b76ef;
    background-color: #0b76ef;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}
.custom-checkbox .prompt {
    position: absolute;
    top: 20px;
    left: 0;
    background: #ffffff;
    pointer-events: none;
    opacity: 0;
    z-index: 3;
    padding: 10px;
}