* {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

img {
  max-width: 100%; }


body {
  background-color: #f7f7ff !important;
  font-family: "mouse-300", Arial, Helvetica, sans-serif;
  /*color: #6A6A6A; */
}

.profile-box {
  width: 1050px;
  margin: 30px auto;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  background-color: #FFF;
  padding-left: 40px;
  padding-right: 20px; }

  .profile-box .left-side {
    /*background-color: #00ab9f;*/
    /*color: #FFF;*/
    padding: 40px 20px;
  }
    .profile-box .left-side .profile-info {
      padding: 5px;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
      background-color: #FFF;
      width: auto;
      /*max-width: 200px;*/
      margin: auto;
      margin-bottom: 20px;
      border-radius: 10px;
      /*color: #444;*/
      text-align: center; }

    .profile-box .left-side .ltitle {
      padding: 10px;
      font-size: 1.1rem;
      border-bottom: 1px solid #CCC; }
    .profile-box .left-side .contact-box {
      display: flex;
      color: black;
      padding: 10px 0px; }
      .profile-box .left-side .contact-box .icon {
        padding: 10px; }
        .profile-box .left-side .contact-box .icon i {
          padding: 10px;
          border: 1px solid #CCC;
          border-radius: 10px;
          width: 10px;
          height: 20px;
          text-align: center;
        }

          .profile-box .left-side .contact-box .detail {
            font-size: .9rem;
            margin: auto 0;
          }


.rit-cover .hotkey {
  text-align: right;
  padding: 30px 20px; }
  .rit-cover .hotkey h1 {
    width: 100%;
    text-align: right;
    /*color: #c7c2c2; */
  }
  .rit-cover .hotkey small {
    font-size: 1rem;
    /*color: #9c9898;*/
    margin-top: -30px;
    font-weight: 600; }
.rit-cover .rit-titl {
  padding: 10px;
  border-bottom: 2px solid #CCC;
  font-size: 1.1rem; }
  .rit-cover .rit-titl i {
    font-size: 18px;
    margin-right: 5px; }
.rit-cover .about {
  hyphens: auto;
  white-space: pre-wrap;
  font-size: .9rem;
  text-align: justify;
  /*text-indent: 15px;*/
  padding: 5px 0px;
  /*padding-left: 40px;*/
  line-height: 1.2rem;
  margin-top: 0;
  margin-bottom: 0;
}

.rit-cover .work-exp {
  padding: 5px 0px; }
  .rit-cover .work-exp h6 {
    font-size: .9rem;
    margin-bottom: 0px; }
    .rit-cover .work-exp h6 span {
      float: right;
      font-size: .8rem; }
  .rit-cover .work-exp i {
    font-size: .9rem; }
  .rit-cover .work-exp ul {
    padding: 5px 0px; }

.rit-cover .education {
  padding: 20px 0px; }
  .rit-cover .education ul li {
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    border-radius: 2px;
    background-color: #ecebeb3d;
    padding: 10px 15px;
    font-size: .9rem; }
    .rit-cover .education ul li span {
      font-size: .8rem; }

